footer{
  display: flex;
  justify-content: space-between;
  align-items: start;
  padding: 20px;
  height: auto;
  background-color: #7A6049;
  color: #C3AA91;
  h3{
    margin-bottom: 10px;
    font-size: 22px;
  }
  ul{
    display: flex;
    flex-direction: column;
    align-items: start;
    row-gap: 5px;
    list-style-type: none;
    font-size: 20px;
  }
  #author{
    margin-top: 90px;
  }
}