.cardroot{
  height: auto;
  width: auto;
  position: relative;

  -webkit-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.75);

  img{
    max-width: 100%;
    height: 100%;

  }
  p{
    color: #e1dcd6;
    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
    z-index: 2;

  }
  &:before{
    content: "";
    position: absolute;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    background: linear-gradient(to top, rgba(122, 96, 73, 1), rgba(122, 96, 73, 0));
    z-index: 1;
  }
}